import React, { useState, useReducer, useEffect } from "react";

function Input() {
  const initialState = { input1: 0, input2: 0 };

  const reducer = (s, a) => ({ ...s, ...a });
  const [state, dispatch] = useReducer(reducer, initialState);

  const handleInput = (e) => {
    dispatch({ [e.target.name]: parseInt(e.target.value) });
    console.log(e.target.name);
  };
  const [total, set_total] = useState(0);

  const x = [state.input1, state.input2];

  const calculateTotal = () => {
    set_total(x.reduce((curr, next) => parseInt(curr) + parseInt(next)));
  };

  return { initialState, total, calculateTotal, handleInput, state };
}
export default Input;
